.recipes-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  grid-gap: 1.5em;
  margin-bottom: 3rem;

  .loader {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h4 {
      color: rgb(170, 170, 170);
    }
  }
}
.recipe-card {
  isolation: isolate;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 5px 15px var(--shadow-color);
  background-color: var(--background-color);

  .recipe-card-info {
    padding: 15px;
  }
  .custom-image {
    z-index: -1;
  }
  .auther-img {
    height: 60px;
    width: 60px;
    border-radius: 50%;
    border: 3px solid var(--background-light);
    box-shadow: 0 1px 3px var(--shadow-color);
    margin-top: -50px;
    z-index: 2;
  }
  .recipe-title {
    font-size: 1.25em;
    font-weight: bold;
    margin-bottom: 0.5em;
    color: var(--text-color);
    margin-top: 0.25em;
  }
  .recipe-desc {
    color: var(--text-light);
    font-size: 0.9em;
    letter-spacing: 1px;
    text-transform: capitalize;
    span {
      font-weight: bold;
    }
  }
  .source {
    margin-top: 0.4em;
    span {
      font-weight: bold;
    }
  }
  .view-btn {
    font-size: 1em;
    font-weight: bold;
    color: var(--primary-color);
    margin-top: 0.75em;
    display: inline-block;
  }
}

@media screen and (max-width: 550px) {
  .recipes-container {
    padding: 0 1em;
  }
}
