.food-categories {
  display: block;
  h2 {
    color: var(--text-color);
    margin-bottom: 0.5em;
  }
  .food-categories-container {
    .search-item {
      background-color: var(--primary-color);
      display: inline-block;
      padding: 0.5em 1em;
      margin: 0 1em 1em 0;
      color: #fff;
      border-radius: 30px;
      box-shadow: 0 3px 6px var(--shadow-color);
      transform: translateX(100%);
      opacity: 0;
      animation: getAnimationDuration(0.5s) slideInRight forwards;

      cursor: pointer;
    }
  }
  .search-box {
    display: flex;
    align-items: center;
    // justify-content: flex-end;
    justify-content: flex-end;
    height: 35px;
    margin-top: 1.5rem;
    div {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    input,
    .btn {
      height: 35px;
    }
    input {
      min-width: 250px;
      padding: 0 0.5em;
      border-radius: 5px 0 0 5px;
      outline: none;
      border: 1px solid #4d4d4d;
    }
    .btn {
      padding-block: 0;
      padding-inline: 0.75em;
      border-radius: 0 5px 5px 0;
      align-content: center;
    }
    // .add-recipe {
    //   height: 40px;
    //   padding: 0 15px;
    //   display: flex;
    //   justify-content: center;
    //   align-items: center;
    //   border-radius: 5px;
    //   outline: none;
    //   border: none;
    //   background-color: var(--text-color);
    //   font-size: 15px;
    //   color: var(--background-color);

    //   cursor: pointer;

    //   svg {
    //     font-size: 20px;
    //     font-weight: bolder;
    //     margin-left: 0.3rem;
    //   }
    // }
  }
}

@media screen and (max-width: 550px) {
  .food-categories {
    padding: 0 1em;
  }
}
