.section.quote {
  display: block;
  .qoute-text {
    font-size: 1.2em;
    line-height: 1.3;
    color: var(--text-color);

    svg {
      color: var(--primary-color);
      font-size: 1.5em;
      margin-right: 0.5em;
    }
  }
  .qoute-auther {
    color: var(--text-light);
    text-align: right;
    margin-top: 1em;
  }
}

@media screen and (max-width: 550px) {
  .quote {
    width: 100%;
    p {
      margin: 0 1em;
    }
  }
}
